/*------------------------------------*\
    #PAGE
\*------------------------------------*/

/**
 * High-, page-level styling. Based on inuit base.page.
 *
 * 1. Set the default `font-size` and `line-height` for the entire project,
 *    sourced from our default variables. The `font-size` is calculated to exist
 *    in ems, the `line-height` is calculated to exist unitlessly.
 * 2. Force scrollbars to always be visible to prevent awkward ‘jumps’ when
 *    navigating between pages that do/do not have enough content to produce
 *    scrollbars naturally.
 * 3. Ensure the page always fills at least the entire height of the viewport.
 * 4. Prevent certain mobile browsers from automatically zooming fonts.
 * 5. Fonts on OSX will look more consistent with other systems that do not
 *    render text using sub-pixel anti-aliasing.
 */
html {
  font-size: ($global-font-size / 16px) * 1em; /* [1] */
  line-height: $global-line-height; /* [1] */
  background-color: $global-color-background-dark;
  color: $global-color-text;
  overflow-y: scroll; /* [2] */
  -webkit-overflow-scrolling: touch;
  height: 100%; /* [3] */
  text-size-adjust: 100%; /* [4] */
  -moz-osx-font-smoothing: grayscale; /* [5] */
  -webkit-font-smoothing: antialiased; /* [5] */
}

body {
  height: 100%;
}

a {
  color: $global-color-link;

  &:visited {
    color: lighten($global-color-link, 20%);
  }

  &:hover,
  &:active {
    color: $global-color-link-hover;
  }
}
