/*------------------------------------*\
    #TOC
\*------------------------------------*/

.toc-link--current {
  background-color: $global-color-text;
  border-top: 1px solid $global-color-theme;
}

.toc--semi-show {
  transform: translate(-$global-spacing-unit--small*2, 0);
}

.toc--show {
  transform: translate(-100%, 0);
}

.toc-mousearea--show {
  z-index: $global-z-index--toc;
  transition: z-index 0s 500ms;
}
