/*------------------------------------*\
    #TOC
\*------------------------------------*/

/*
 * Table of Contents
 */
.toc-wrapper {
  position: fixed;
  // menu mask on +1
  z-index: $global-z-index--toc;
  top: 12%;
  left: 101%;
  max-width: 300px;
  max-height: 100%;
  padding-left: $global-spacing-unit--small;
  padding-bottom: $global-spacing-unit;
  transition: all 0.3s;
  transform: translate(-$global-spacing-unit--small, 0);
}

.toc-mousearea {
  position: fixed;
  z-index: -1;
  top: 0;
  bottom: 0;
  right: 0;
  width: 17 / 24 * $global-article-padding--large * 0.5; // subtracted menu width

  @include breakpoint($breakpoint-handheld) {
    display: none;
  }
}

.toc-container {
  overflow: hidden;
  padding: $global-spacing-unit 0;
  background: $global-color-background-dark;
  box-shadow: 1px 4px 11px 0 rgba(0, 0, 0, 0.4);
  opacity: .9;

  // fixed unintented height
  li {
    display: block;
  }

  a,
  a:visited,
  a:hover,
  a:active,
  a:focus {
    color: $global-color-background;
    outline: 0;
  }
}

.toc {
  ol {
    margin-left: $global-spacing-unit--small;
  }
}

.toc-child {
  font-size: 0.86em;
}

.toc-link {
  display: block;
  overflow: hidden;
  padding: 0 $global-spacing-unit 0 $global-spacing-unit--small;
  white-space: nowrap;
  border-top: 1px solid transparent;
  -webkit-tap-highlight-color: transparent;

  @include breakpoint($breakpoint-monitor) {
    padding-right: $global-spacing-unit--large;
  }
}

@import 'toc/states';
